import React, { useState } from "react";
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import TextField from "../common/FormsyTextField";

const useStyles = makeStyles(() => ({
    imageURL: {
        width: "100%",
        marginTop: 16,
        marginBottom: 16,
    },
}));

const AddImageDialog = (props) => {
    const { open, onCancel, onResult } = props;
    const [imageURL, setImageURL] = useState("");
    const [validURL, setValidURL] = useState(false);
    const classes = useStyles();

    const handleImageURLChange = (event) => {
        setImageURL(event.target.value);
    };

    const handleResult = () => {
        onResult(imageURL);
    };

    return (
        <Dialog open={open}>
            <DialogTitle>Add Image</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Please specify an image with 1:1 aspect ratio, that is, an
                    image with equal width and height. This is a convenient
                    format for mobile screens.
                </DialogContentText>
                <Formsy
                    onValid={() => setValidURL(true)}
                    onInvalid={() => setValidURL(false)}>
                    <TextField
                        name="imageUrl"
                        className={classes.imageURL}
                        label="Image URL"
                        validations={{
                            isUrl: true,
                        }}
                        validationErrors={{
                            isUrl: "Please enter a valid image URL.",
                        }}
                        variant="outlined"
                        size="small"
                        value={imageURL}
                        onChange={handleImageURLChange}
                        required={true}
                    />
                </Formsy>
            </DialogContent>
            <DialogActions>
                <Button onClick={onCancel} color="primary">
                    Cancel
                </Button>
                <Button
                    onClick={handleResult}
                    color="primary"
                    disabled={!validURL}>
                    Add
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default AddImageDialog;
