import {
    Button,
    FormControl,
    MenuItem,
    InputLabel,
    Select,
    CircularProgress,
    Chip,
    Typography,
    Avatar,
} from "@material-ui/core";
import { Autocomplete, createFilterOptions } from "@material-ui/lab";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector, useDispatch } from "react-redux";
import { useParams, useHistory } from "react-router-dom";
import Formsy from "formsy-react";

import {
    fetchArticle,
    createArticle,
    updateArticle,
    uploadToSignedURL,
    getUploadSignedURL,
    showNotification,
    publishArticle,
    unpublishArticle,
} from "../../redux/actions";
import Image from "./Image";
import AddImage from "./AddImage";
import AddImageDialog from "./AddImageDialog";
import { languageCodes } from "../../common/constants";
import { getUsers } from "../../server/api";
import TextField from "../common/FormsyTextField";

const useStyles = makeStyles(() => ({
    container: {
        padding: 32,
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        minHeight: "calc(100vh - 64px)",
        width: "calc(100vw - 16px - 256px - 16px - 16px)",
        marginLeft: 256 + 16 + 16,
        backgroundColor: "white",
        borderRadius: 16,
    },
    left: {
        display: "flex",
        flexDirection: "column",
        width: "70%",
    },
    imagesContainer: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "flex-start",
        alignItems: "center",
    },
    image: {},
    description: {
        width: "100%",
        marginTop: 40,
    },
    transcript: {
        width: "100%",
        marginTop: 24,
    },
    descriptionInput: {
        lineHeight: 1.8,
        fontSize: 15,
    },
    transcriptInput: {
        lineHeight: 1.8,
        fontSize: 15,
    },
    right: {
        display: "flex",
        flexDirection: "column",
        marginLeft: 24,
        flex: 1,
    },
    title: {
        width: "100%",
    },
    narrator: {
        width: "100%",
        marginTop: 24,
    },
    primaryImageURL: {
        width: "100%",
        marginTop: 24,
    },
    genderGroup: {
        marginTop: 16,
    },
    genderGroupControls: {
        display: "flex",
        flexDirection: "row",
    },
    languageCode: {
        marginTop: 24,
        width: "100%",
    },
    audioPlayer: {
        outline: "none",
        marginTop: 40,
        width: "100%",
    },
    publish: {
        width: "100%",
        marginTop: 40,
    },
    save: {
        width: "100%",
        marginTop: 24,
    },
    attachAudio: {
        width: "100%",
        marginTop: 24,
    },
    progress: {
        marginRight: 8,
    },
    chips: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
    },
    chip: {
        margin: 2,
        fontSize: 11,
    },
    tags: {
        marginTop: 24,
        width: "100%",
    },
    narratorOption: {
        flexDirection: "row",
        display: "flex",
        paddingTop: 4,
        paddingBottom: 4,
    },
    narratorImage: {
        width: 40,
        height: 40,
        borderRadius: 20,
    },
    narratorTexts: {
        display: "flex",
        flexDirection: "column",
        marginLeft: 16,
    },
    narratorName: {
        fontSize: 14,
        color: "#424242",
    },
    narratorUserName: {
        fontSize: 12,
        color: "#9e9e9e",
    },
    articleTagImage: {
        width: 28,
        height: 28,
        borderRadius: "50%",
        marginRight: 12,
    },
    loadingArticleTags: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "100%",
        height: 56,
    },
}));

const arraysEqual = (a, b) => {
    if (a === b) return true;
    if (a == null || b == null) return false;
    if (a.length !== b.length) return false;

    // If you don't care about the order of the elements inside
    // the array, you should sort both arrays here.
    // Please note that calling sort on an array will modify that array.
    // you might want to clone your array first.

    /* eslint-disable-next-line no-plusplus */
    for (let i = 0; i < a.length; ++i) {
        if (a[i] !== b[i]) {
            return false;
        }
    }

    return true;
};

const EditArticle = () => {
    const params = useParams();
    const {
        current,
        uploadSignedURL,
        saving,
        publishing,
        generating,
        uploading,
        error,
    } = useSelector((state) => state.article);
    const {
        list: articleTags,
        map: articleTagById,
        fetching: fetchingArticleTags,
    } = useSelector((state) => state.articleTags);
    const dispatch = useDispatch();
    const audioRef = useRef();
    const classes = useStyles();
    const history = useHistory();
    const [description, setDescription] = useState("");
    const [transcript, setTranscript] = useState("");
    const [title, setTitle] = useState("");
    const [narrator, setNarrator] = useState(null);
    const [imageURLs, setImageURLs] = useState([]);
    const [languageCode, setLanguageCode] = useState("en");
    const [loadingFile, setLoadingFile] = useState(false);
    const [audioData, setAudioData] = useState(null);
    const [validAudio, setValidAudio] = useState(true);
    const [openAddImageDialog, setOpenAddImageDialog] = useState(false);
    const [selectedTags, setSelectedTags] = useState([]);
    const [narratorOptions, setNarratorOptions] = useState([]);
    const [narratorOptionsOpen, setNarratorOptionsOpen] = useState(false);
    // const [emptyNarratorResults, setEmptyNarratorResults] = useState(false);
    const [search, setSearch] = useState("");
    const [validSave, setValidSave] = useState(false);
    const fileInputRef = useRef();
    const savingRef = useRef();
    const publishingRef = useRef();
    const generatingRef = useRef();
    const uploadingRef = useRef();

    const narratorFilterOptions = createFilterOptions({
        matchFrom: "any",
        ignoreAccents: true,
        ignoreCase: true,
        stringify: (option) =>
            `${option.userName} ${option.firstName} ${option.lastName}`,
    });

    const loadingNarratorOptions =
        narratorOptionsOpen && narratorOptions.length === 0;

    useEffect(() => {
        if (!loadingNarratorOptions) {
            return undefined;
        }

        let active = true;

        (async () => {
            let records = [];
            if (search) {
                const users = await getUsers({
                    search,
                });
                console.log(users);
                records = users.records;
            }

            if (active) {
                // if (records.length === 0) {
                //   setEmptyNarratorResults(true);
                // }
                setNarratorOptions(records);
            }
        })();

        return () => {
            active = false;
        };
    }, [loadingNarratorOptions, search]);

    useEffect(() => {
        if (!narratorOptionsOpen) {
            setNarratorOptions([]);
        }
    }, [narratorOptionsOpen]);

    const persistedArticle = params.id && current?.id === params.id;
    const changesMade =
        !params.id ||
        (current &&
            (current.transcript !== transcript ||
                current.description !== description ||
                current.title !== title ||
                current.narrator.id !== narrator?.id ||
                !arraysEqual(current.tags, selectedTags) ||
                !arraysEqual(current.imageURLs, imageURLs) ||
                current.languageCode !== languageCode));

    useEffect(() => {
        /* 1. Were we saving something previously?
         * 2. Was the save successful?
         * 3. Sure we have an article?
         */
        if (savingRef.current && !saving && !error && current) {
            history.replace(`/articles/${current.id}/edit`);
        }
        savingRef.current = saving;
    }, [current, error, history, saving]);

    useEffect(() => {
        if (publishingRef.current && !publishing && error) {
            dispatch(showNotification(error, "ERROR"));
        }
        publishingRef.current = publishing;
    }, [dispatch, error, publishing]);

    useEffect(() => {
        if (params.id && (!current || current.id !== params.id)) {
            dispatch(
                fetchArticle({
                    id: params.id,
                }),
            );
        }
    }, [current, dispatch, params.id]);

    useEffect(() => {
        if (generatingRef.current && !generating && !error && uploadSignedURL) {
            dispatch(uploadToSignedURL(uploadSignedURL, audioData));
        }

        if (generatingRef.current && !generating && error) {
            dispatch(showNotification(error, "ERROR"));
        }

        generatingRef.current = generating;
    }, [audioData, dispatch, error, generating, uploadSignedURL]);

    useEffect(() => {
        if (uploadingRef.current && !uploading) {
            setValidAudio(true);
            audioRef.current?.load();
        }
        uploadingRef.current = uploading;
    }, [uploading]);

    useEffect(() => {
        if (persistedArticle) {
            setTranscript(current.transcript);
            setDescription(current.description);
            setTitle(current.title);
            setNarrator(current.narrator);
            setSelectedTags(current.tags || []);
            setImageURLs(current.imageURLs);
            setLanguageCode(current.languageCode);
        }
    }, [current, persistedArticle]);

    useEffect(() => {
        audioRef.current?.load();
    }, []);

    console.log(articleTags);

    const handleOpenAddImageDialog = () => {
        setOpenAddImageDialog(true);
    };

    const handleCloseAddImageDialog = () => {
        setOpenAddImageDialog(false);
    };

    const handleNewImage = (imageURL) => {
        setImageURLs([...imageURLs, imageURL]);
        setOpenAddImageDialog(false);
    };

    const handleDeleteImage = (index) => () => {
        const newImageURLs = imageURLs.filter(
            (_value, currentIndex) => currentIndex !== index,
        );
        setImageURLs(newImageURLs);
    };

    const handleFileReset = (event) => {
        /* Reset the input element. Otherwise the user will not be able to select the same file again.
         * https://stackoverflow.com/a/40429197
         */
        /* eslint-disable-next-line no-param-reassign */
        event.target.value = null;
    };

    const handleFileUpload = async (event) => {
        if (
            !window.File ||
            !window.FileReader ||
            !window.FileList ||
            !window.Blob
        ) {
            alert("The File APIs are not fully supported in this browser.");
            return;
        }

        setLoadingFile(true);
        const file = event.target.files[0];
        const result = await file.arrayBuffer();
        setLoadingFile(false);
        setAudioData(result);

        const confirmation = window.confirm(
            `Do you want to upload ${file.name}?`,
        );
        if (confirmation) {
            dispatch(getUploadSignedURL(current.slug));
        }
    };

    const handleTitleChange = (event) => {
        setTitle(event.target.value);
    };

    const handleNarratorChange = (event, newNarrator) => {
        setNarrator(newNarrator);
    };

    const handleNarratorOptionsOpen = () => {
        // setEmptyNarratorResults(false);
        setNarratorOptionsOpen(true);
    };

    const handleNarratorOptionsClose = () => setNarratorOptionsOpen(false);

    const handleTranscriptChange = (event) => {
        setTranscript(event.target.value);
    };

    const handleDescriptionChange = (event) => {
        setDescription(event.target.value);
    };

    const handleLanguageCodeChange = (event) => {
        setLanguageCode(event.target.value);
    };

    const handleAudioError = () => {
        setValidAudio(false);
    };

    const handleSave = () => {
        const articleData = {
            title,
            transcript,
            description,
            narrator: narrator.id,
            tags: selectedTags,
            imageURLs,
            languageCode,
        };
        let actionCreator = createArticle;
        if (params.id) {
            articleData.id = params.id;
            actionCreator = updateArticle;
        }
        dispatch(actionCreator(articleData));
    };

    const handlePublish = () => {
        const actionCreator = current.published
            ? unpublishArticle
            : publishArticle;
        dispatch(actionCreator(current.id));
    };

    const renderLanguageCodeSelect = () => (
        <FormControl
            variant="outlined"
            className={classes.languageCode}
            size="small"
            required={true}>
            <InputLabel id="languageCodeLabel">Language</InputLabel>
            <Select
                labelId="languageCodeLabel"
                value={languageCode}
                onChange={handleLanguageCodeChange}
                label="Language">
                {languageCodes.map((languageCode0) => (
                    <MenuItem
                        key={languageCode0.locale}
                        value={languageCode0.locale}>
                        {languageCode0.title}
                    </MenuItem>
                ))}
            </Select>
        </FormControl>
    );

    const handleTagsChange = (event) => {
        if (!fetchingArticleTags) {
            setSelectedTags([...event.target.value]);
        }
    };

    const tagsMenuProps = {
        PaperProps: {
            style: {
                maxHeight: 300,
                width: 240,
            },
        },
    };

    const renderTags = () => (
        <FormControl className={classes.tags} variant="outlined" size="small">
            <InputLabel
                style={{
                    backgroundColor: "white",
                    paddingLeft: 4,
                    paddingRight: 4,
                }}
                id="tags-label">
                Tags
            </InputLabel>
            <Select
                labelId="tags-label"
                multiple={true}
                value={selectedTags}
                onChange={handleTagsChange}
                renderValue={() => (
                    <div className={classes.chips}>
                        {selectedTags.map((tagId) => {
                            const articleTag = articleTagById[tagId];
                            return (
                                <Chip
                                    key={tagId}
                                    label={articleTag.title}
                                    className={classes.chip}
                                    avatar={
                                        <Avatar src={articleTag.imageURL} />
                                    }
                                />
                            );
                        })}
                    </div>
                )}
                MenuProps={tagsMenuProps}>
                {articleTags.length === 0 && (
                    <div className={classes.loadingArticleTags}>
                        <CircularProgress size="24px" />
                    </div>
                )}
                {articleTags.length > 0 &&
                    articleTags.map((tag) => (
                        <MenuItem key={tag.id} value={tag.id}>
                            <img
                                className={classes.articleTagImage}
                                src={tag.imageURL}
                            />
                            <Typography>{tag.title}</Typography>
                        </MenuItem>
                    ))}
            </Select>
        </FormControl>
    );

    const renderPlayer = () => {
        if (!persistedArticle) {
            return null;
        }
        return (
            <audio
                controls={true}
                className={classes.audioPlayer}
                ref={audioRef}
                onError={handleAudioError}>
                <source src={current.audioURL} type="audio/wav" />
            </audio>
        );
    };

    const renderAttachButton = () => {
        if (!persistedArticle) {
            return null;
        }

        const processing = loadingFile || generating || uploading;

        return (
            <>
                <input
                    ref={fileInputRef}
                    type="file"
                    accept="audio/*"
                    multiple={false}
                    onChange={handleFileUpload}
                    onClick={handleFileReset}
                    hidden={true}
                />
                <Button
                    variant="contained"
                    color="primary"
                    className={classes.attachAudio}
                    onClick={() => fileInputRef.current?.click()}
                    disabled={processing}>
                    {processing && (
                        <CircularProgress
                            size={8}
                            className={classes.progress}
                        />
                    )}
                    {loadingFile && "Loading file..."}
                    {generating && "Preparing to upload..."}
                    {uploading && "Uploading..."}
                    {!processing && "Attach Audio"}
                </Button>
            </>
        );
    };

    const renderSave = () => (
        <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.save}
            onClick={handleSave}
            disabled={!validSave || !narrator || saving}>
            {saving && (
                <CircularProgress size={16} className={classes.progress} />
            )}
            Save
        </Button>
    );

    const renderPublish = () =>
        persistedArticle &&
        validAudio &&
        imageURLs.length > 0 &&
        !changesMade && (
            <Button
                variant="contained"
                color="primary"
                className={classes.publish}
                onClick={handlePublish}>
                {current.published ? "Unpublish" : "Publish"}
            </Button>
        );

    const renderImages = () => (
        <div className={classes.imagesContainer}>
            {imageURLs.map((imageURL, index) => (
                <Image
                    key={imageURL}
                    imageURL={imageURL}
                    onDelete={handleDeleteImage(index)}
                    className={classes.image}
                />
            ))}
            <AddImage onClick={handleOpenAddImageDialog} />
        </div>
    );

    const renderNarratorSelect = () => (
        <Autocomplete
            size="small"
            filterOptions={narratorFilterOptions}
            open={narratorOptionsOpen}
            onOpen={handleNarratorOptionsOpen}
            onClose={handleNarratorOptionsClose}
            renderOptionLabel={(option) => (
                <React.Fragment>
                    {`${option.userName} â€¢ ${option.firstName} ${option.lastName}`}
                </React.Fragment>
            )}
            renderOption={(option) => (
                <div className={classes.narratorOption}>
                    <img
                        className={classes.narratorImage}
                        src={option.pictureURL}
                    />
                    <div className={classes.narratorTexts}>
                        <span className={classes.narratorName}>
                            {option.firstName} {option.lastName}
                        </span>
                        <span className={classes.narratorUserName}>
                            {option.userName}
                        </span>
                    </div>
                </div>
            )}
            className={classes.narrator}
            options={narratorOptions}
            loading={loadingNarratorOptions}
            renderInput={(params0) => (
                <TextField
                    name="narrator"
                    {...params0}
                    label="Narrator"
                    variant="outlined"
                    InputProps={{
                        ...params0.InputProps,
                        endAdornment: (
                            <React.Fragment>
                                {loadingNarratorOptions ? (
                                    <CircularProgress
                                        color="inherit"
                                        size={20}
                                    />
                                ) : null}
                                {params0.InputProps.endAdornment}
                            </React.Fragment>
                        ),
                    }}
                    onChange={(event) => setSearch(event.target.value)}
                />
            )}
            getOptionSelected={(option, value) => option.id === value.id}
            getOptionLabel={(option) => option.userName || "unavailable"}
            value={narrator}
            onChange={handleNarratorChange}
        />
    );

    return (
        <>
            <Formsy
                onValid={() => setValidSave(true)}
                onInvalid={() => setValidSave(false)}>
                <div className={classes.container}>
                    <div className={classes.left}>
                        {renderImages()}
                        <TextField
                            name="description"
                            className={classes.description}
                            label="Description"
                            validations={{
                                minLength: 10,
                                maxLength: 1000,
                            }}
                            validationErrors={{
                                minLength:
                                    "The description should have 10-1000 characters.",
                                maxLength:
                                    "The description should have 10-1000 characters.",
                            }}
                            multiline={true}
                            rows={5}
                            variant="outlined"
                            size="small"
                            value={description}
                            onChange={handleDescriptionChange}
                            InputProps={{
                                classes: {
                                    input: classes.descriptionInput,
                                },
                            }}
                            required={true}
                        />
                        <TextField
                            name="transcript"
                            className={classes.transcript}
                            label="Transcript"
                            validations={{
                                minLength: 10,
                                maxLength: 20000,
                            }}
                            validationErrors={{
                                minLength:
                                    "The transcript should have 10-20000 characters.",
                                maxLength:
                                    "The transcript should have 10-20000 characters.",
                            }}
                            multiline={true}
                            rows={30}
                            variant="outlined"
                            size="small"
                            value={transcript}
                            onChange={handleTranscriptChange}
                            InputProps={{
                                classes: {
                                    input: classes.transcriptInput,
                                },
                            }}
                            required={true}
                        />
                    </div>
                    <div className={classes.right}>
                        <TextField
                            name="title"
                            className={classes.title}
                            label="Title"
                            validations={{
                                minLength: 10,
                                maxLength: 200,
                            }}
                            validationErrors={{
                                minLength:
                                    "The title should have 10-200 characters.",
                                maxLength:
                                    "The title should have 10-200 characters.",
                            }}
                            variant="outlined"
                            size="small"
                            value={title}
                            onChange={handleTitleChange}
                            required={true}
                        />
                        {renderNarratorSelect()}
                        {renderTags()}
                        {renderLanguageCodeSelect()}
                        {renderSave()}

                        {renderPlayer()}
                        {renderAttachButton()}

                        {renderPublish()}
                    </div>
                </div>
            </Formsy>
            {openAddImageDialog && (
                <AddImageDialog
                    open={true}
                    onCancel={handleCloseAddImageDialog}
                    onResult={handleNewImage}
                />
            )}
        </>
    );
};

export default EditArticle;
